

import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
props: ['placeholder','text','noLink'],
components: {
XInput, XHeader, Icon, Flexbox, FlexboxItem
},
methods: {
search(){
if(this.key){
if(this.isLink){
this.$router.push('/Search/Result/' + this.key);
}else{
this.$emit('search',this.key);
}
}
}
},
mounted(){
if(typeof this.noLink != 'undefined'){
this.isLink = false
}
},
watch:{
text(newVal, oldVal){
this.key=newVal;
}
},
data () {
return {
isLink:true,
key:this.text
}
}
}




